
.game-main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding-bottom: 80px; // Space for character bar
}

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: var(--spacing-md);
  gap: var(--spacing-lg);
  overflow-y: auto;
}

// Responsive
@media (min-width: 1024px) {
  .game-main {
    padding-bottom: 0;
  }

  .main-content {
    max-width: 800px;
    margin: 0 auto;
    width: 100%;
    padding: var(--spacing-xl);
  }
}
