
.background-layer {
  position: fixed;
  inset: 0;
  z-index: -1;
  overflow: hidden;
  pointer-events: none;
}

.bg-gradient {
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, var(--primary-bg) 0%, var(--secondary-bg) 50%, var(--primary-bg) 100%);
}

.bg-pattern {
  position: absolute;
  inset: 0;
  opacity: 0.03;
  background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23c9a86c' fill-opacity='0.4'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

.corner-decor {
  position: absolute;
  opacity: 0.6;
  transition: opacity var(--transition-slow);

  &.corner-top-left {
    top: 0;
    left: 0;
  }

  &.corner-top-right {
    top: 0;
    right: 0;
    transform: scaleX(-1);
  }

  &.corner-bottom-left {
    bottom: 0;
    left: 0;
    transform: scaleY(-1);
  }

  &.corner-bottom-right {
    bottom: 0;
    right: 0;
    transform: scale(-1, -1);
  }
}

.particles {
  position: absolute;
  inset: 0;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--decor-gold);
  border-radius: 50%;
  opacity: 0;
  animation: particleFloat 4s ease-in-out infinite;

  &::before {
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    background: radial-gradient(circle, var(--decor-gold) 0%, transparent 70%);
    border-radius: 50%;
    transform: translate(-2px, -2px);
  }
}

@keyframes particleFloat {
  0%,
  100% {
    opacity: 0;
    transform: translateY(0) scale(0.5);
  }
  50% {
    opacity: 0.6;
    transform: translateY(-30px) scale(1);
  }
}

// Responsive adjustments
@media (max-width: 767px) {
  .corner-decor {
    width: 80px;
    height: 80px;
    opacity: 0.4;
  }

  .particle {
    display: none;
  }
}
